import React, { useState } from 'react'
import Axios from 'axios'
import Recipe from './Recipe'

const Header = () => {

  const [query, setQuery] = useState('');
  const [recipes, setRecipes] = useState([]);

  const APP_ID = "05ebc106";
  const APP_KEY = "ffb8e76cc49fed2199358e9060fde6d5";
  const url = `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`;

  const getData = async () => {
    await Axios.get(url)
    .then(response => {
     // console.log(response);
     setQuery('');
     setRecipes(response.data.hits);
    })
    .catch(error => {
      alert('Please enter a correct food name');
    })
  };
  
  const onInputChange = (e) => {
    setQuery(e.target.value);
  // console.log(e.target.value);
  }

  const onSubmit = (event) => {
    event.preventDefault();
    if (query === ''){
      alert('Please enter a food name');
    }else {
      getData();
     
    }
   
  };

return (
<>
<div className="container-fluid" style={{background: 'linear-gradient(90deg, #141e30, #243b55)', textAlign: 'center', color: 'white', padding: '50px'}}>
<h1 className="display-4">
  <div className="material-icons" style={{fontSize: 50}}>fastfood</div>
   &nbsp;  Food Receipe</h1>

 <form onSubmit={onSubmit}>
<div className="input-group w-50 mx-auto" style={{marginTop: 32}}>
  <input type="text" 
  className="form-control" 
  placeholder="Search your food recipe..." 
  autoComplete='off' 
  onChange={onInputChange} 
  value={query}
 /> &nbsp;

    <button 
    className="btn btn-info" onClick={getData}>Search</button>
  </div>
  </form>
  </div>
 
  
 
<div className='container-fluid'>
   <Recipe recipes={recipes} />
</div>
 
  
  
</>
)}

export default Header;
