import React from 'react'

const Recipe = (props) => {

    return (
<div className='row' style={{background: 'linear-gradient(90deg, #fff, #fff)', fontFamily: 'nunito sans', padding: '20px'}}>
            {props.recipes.map(recipe =>
<div key={recipe.recipe.id} className='col-xs-6 col-sm-6 col-md-4 col-lg-3 col-xl-3'>
<div className="card" style={{marginBottom: 40, backgroundColor: '#f7fcfe', height: '90%'}}>
<div className="card-header">  
<img src={recipe.recipe.image} className='img-fluid  w-100 img-thumbnail' alt={recipe.recipe.label} style={{borderRadius: '18px',border: '1px solid lavender'}} />
</div>
  <div className="card-body">
  <h6><b>Title:</b> {recipe.recipe.label}</h6>
  <h6><b>Cuisine Type:</b> {recipe.recipe.cuisineType}</h6>
  <h6><b>Meal Type:</b> {recipe.recipe.mealType}</h6>
  <h6><b>Source:</b> {recipe.recipe.source}</h6>
<a href={recipe.recipe.shareAs} target='_blank' rel="noopener noreferrer" style={{fontSize: '16px', color: 'blue'}}>Visit Website</a>
  </div>
</div>
</div>
 )}
</div>
)
}

export default Recipe